@media (prefers-reduced-motion: no-preference) {
  * {
    scroll-behavior: smooth;
  }
}

@media only screen and (max-width: 400px) {
  header {
    height: 100px !important;
  }

  main {
    padding-top: 100px !important;
    /* header 50px height*/
  }
}

/* h2 is About */
h2 {
  text-align: left;
  font-family: Verdana, Tahoma;
  letter-spacing: 1.5px;
  border-bottom: 4px solid #dfdfe2;
  max-width: 80%;
  margin-top: 0px;
  padding-top: 10px;
}

h2::before {
  content: "//";
}

.info {
  padding: 0.5px;
}

/* About */

main {
  padding-top: 50px;
  /* header 50px height*/
}

body {
  text-align: center;
  align-items: center;
  background: #1b1b32;
  color: #efefef;
  font-family: Helvetica;
  margin: 0;
}

body a {
  color: #efefef;
  text-decoration: none !important;
}

body a:hover {
  text-decoration: underline;
  background-color: #f1be32;
  color: #1b1b32;
}

.definition {
  width: 85%;
  text-align: justify;
  padding: 2%;
  margin-left: auto;
  margin-right: auto;
  color: #1b1b32;
  border-radius: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.info {
  width: 80%;
  text-align: justify;
  padding: 0% 5% 0% 5%;
  margin: 0% auto 10px auto;

}

@media (min-width: 800px) {

  .keypad,
  .control {
    width: 45%;
  }
}

@media (max-width: 799px) {

  .keypad,
  .control {
    width: 80%;
  }
}

.keypad {
  max-width: 400px;
  width: 100%;
  aspect-ratio: 1/1;
  text-align: justify;
  padding: 3%;
  background-color: #aaaaaa;
  color: #1b1b32;
  border-radius: 15px;

  /* Use CSS Grid to create a 3x3 grid */
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  /* Adjust the gap between drum pads as needed */
}

.keypad .drum-pad {
  aspect-ratio: 1/1;
  font-family: Verdana, Tahoma;
  font-weight: bold;
  background-color: #417725;
  border: 5px outset #272929;
  border-radius: 15px;

  /* Buttton related effects*/
  cursor: pointer;
  box-shadow: 5px 5px #888888;

  /* Use flexbox to center content and make {id} center */
  display: flex;
  align-items: center;
  justify-content: center;

  /* https://stackoverflow.com/questions/30693928/how-to-make-font-size-relative-to-parent-div */
  container-type: size;
}

.keypad .drum-pad:hover {
  background-image: linear-gradient(rgba(192, 192, 192, 0.4) 0 0);
}

.keypad .drum-pad:active {
  background-image: linear-gradient(rgba(192, 192, 192, 0.6) 0 0);
  transform: translate(4px, 4px);
  box-shadow: 1px 1px #888888;
}

.keypad .drum-pad span {
  resize: both;
  font-size: 45cqmin;
}

.control {
  max-width: 400px;
  width: 100%;
  aspect-ratio: 1/1;
  text-align: justify;
  padding: 3%;
  background-color: #aaaaaa;
  color: #1b1b32;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.control .panel {
  font-family: Verdana, Tahoma;
  font-weight: bold;

  height: 25%;
  text-align: center;
  padding: 3%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  container-type: size;
}

.control .panel .tableOutline {
  width: 90%;
  height: 4rem;
  margin: auto;
  padding: 2%;
  background-color: #9bbc0f;
  color: #1b1b32;
  border: 5px outset #272929;
  border-radius: 5px;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  resize: both;
  font-size: 30cqmin;
}

/*input[type='range'] {}*/
#volumecontrolbar {
  width: 340px;
  accent-color: #417725;
  cursor: pointer;
}

#volumecontrolbar::-webkit-slider-runnable-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  box-shadow: 1px 1px 1px #000000, 1px 1px 1px #888888;
  border-radius: 3px;
  border: 1px outset #272929;
}

#volumecontrolbar::-webkit-slider-thumb {
  text-align: center;
  margin-top: -4.2px;
}

/*on off switch*/
.switchDiv {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.switch {
  position: relative;
  display: inline-block;
  width: 120px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #888888;
  -webkit-transition: .4s;
  transition: .4s;
  border: 5px outset #272929;
  border-radius: 15px;
}

.slider:hover {
  background-image: linear-gradient(rgba(0, 0, 0, 0.4) 0 0);
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: -5px;
  bottom: -5px;
  border: 5px outset #272929;
  border-radius: 50px;
  background-color: #1b1b32;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked+.slider {
  background-color: #417725;
}

input:focus+.slider {
  box-shadow: 0 0 1px #417725;
}

input:checked+.slider:before {
  -webkit-transform: translateX(calc(120px - 26px - 5px));
  -ms-transform: translateX(calc(120px - 26px - 5px));
  transform: translateX(calc(120px - 26px - 5px));
}